
@import 'variables.scss';

$input-font-size: $font-size-normal;
$form-height: 24px;
$form-padding-horizontal: 8px;
$form-padding-vertical: 4px;
$background-color-input: $grey-2;
$background-color-search: $grey-1;

.MuiInput-root, .MuiInputBase-root {
  border: none;

  &::after {
    display: none;
  }

  &::before {
    display: none;
    border-bottom: 0px solid transparent;
  }
}

.MuiInputBase-root>.salalah-input-box, .MuiInputBase-root.salalah-input-box>Input {
  background-color: $background-color-input;
  border-radius: $box-radius-1;
  height: $form-height;
  padding: $form-padding-vertical $form-padding-horizontal;
  font-size: $input-font-size;
  line-height: 24px;
}

.MuiInputBase-root.salalah-search-box>Input, .MuiInputBase-root.salalah-search-box {
  background-color: $background-color-search;
  border-radius: $box-radius-2;
}
