@import './../../../assets/style/variables.scss';
$filter-card-background: $grey-2;
$filter-card-background-hover: white;

.filters-wrapper {
  .filter-item {
    padding: 16px;
    background-color: $filter-card-background;
    cursor: pointer;
    color: $secondary-color;
    &:hover {
      background-color: $filter-card-background-hover;
    }
    &.active {
      color: $primary-color;
      background-color: $filter-card-background-hover;
    }
    h3 {
      padding-bottom: 8px;
    }
  }
}


//users css
.user-space {
  &>div {
    cursor: pointer;
  }
}

//datepicker css
.date-range-picker-wrapper {
  margin-bottom: 16px;
  box-shadow: $box-shadow-2;

  .date-range-picker {
    padding: 16px;
    justify-content: space-between;

    .react-datepicker-wrapper {
      width: 100px;

      .react-datepicker__input-container {
        width: 100px;

        input {
          width: 100px !important;
          padding: 0px;
          margin: 0px;
          border: none;
          color: $secondary-color;
          font-size: $font-size-normal;
          outline: none;
        }
      }

      &:first-child {
        float: left;
      }

      &:last-child, &:nth-child(2) {
        float: right;

        .react-datepicker__input-container > input {
          text-align: right;
        }
      }
    }
  }
  .clear-float {
    clear: both;
    height: 15px;
    z-index: 1;
    position: relative;
    &:after {
      content: '-';
      color: $secondary-color;
      position: absolute;
      top: -29px;
      font-size: $font-size-xl;
    }
  }
}


// data list css
.List-container {
  background-color: white !important;
  box-shadow: $box-shadow-2;
  border-radius: $box-radius-1;
  .List-item {
    padding: 0px 16px;
    border-bottom: 1px solid $grey-1;
    border-left: 4px solid transparent;
    cursor: pointer;
    &.pending {
      border-left: 4px solid $red-1;
    }
    &.active {
      border-left: 4px solid $yellow-1;
    }
    &.completed {
      border-left: 4px solid $green-1;
    }
    &:hover {
      background: $primary-color;
      color: white !important;
    }
  }
}